export default function Empty({ data }) {
  return (
    <div class="grid h-screen px-4 place-content-center">
      <h1 class="tracking-widest text-gray-500 uppercase">
        {data?.status ?? "Empty"} |{" "}
        {data?.text ?? "No Content For This Section."}
      </h1>
    </div>
  );
}
