import { Link } from "@remix-run/react";

export default function Top({ data }) {
  return (
    <div className="flex flex-wrap ">
      <section className="lg:w-[50%] lg:h-1/2">
        <video className="w-full h-full" loop autoPlay muted>
          <source src="/car.mp4" type="video/mp4" />
        </video>
      </section>
      <section className="lg:w-[50%]  lg:h-1/2 ">
        <div className="ml-[40px]">
          <div>
            <h2 className="top-h2 ">
              {data?.shortdesc ?? "Short Description"}
            </h2>
            <Link
              to={data?.weblink ?? "#"}
              className="flex top-link dashboard-title-section fromLeft"
            >
              Live Web
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                strokeWidth="1.5"
                stroke="currentColor"
                className="w-[30px] h-[30px]"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M4.5 19.5l15-15m0 0H8.25m11.25 0v11.25"
                />
              </svg>
            </Link>
          </div>
          test test test test
        </div>
      </section>
    </div>
  );
}
