import { json } from "@remix-run/node";
import {
  Form,
  Link,
  NavLink,
  Outlet,
  ScrollRestoration,
  isRouteErrorResponse,
  useLoaderData,
  useLocation,
  useParams,
  useRouteError,
} from "@remix-run/react";
import { AnimatePresence, motion } from "framer-motion";
import Top from "../components/Projects/id/Top";

export const loader = async ({ params }) => {
  const userId = params.id;
  const noteListItems = [
    {
      id: "overview",
      title: "Overview",
    },
    {
      id: "approach",
      title: "Development Approach",
    },
    {
      id: "gallery",
      title: "Gallery",
    },
  ];
  return json({ userId, noteListItems });
};

export default function ProjectId() {
  const data = useLoaderData();
  const location = useLocation();
  const pathname = location?.pathname;
  return (
    // <AnimatePresence>
    <article className="section-pad w-full project-base ">
      <h1 className="text-[60px] lg:text-[100px]">Project {data.userId}</h1>
      <main className="flex-row h-full ">
        <div className="h-auto w-full  ">
          <Top />
          <ol className="flex w-full  gap-4 mt-[20px] justify-center rounded-full ">
            {data.noteListItems.map((note) => (
              <li key={note.id}>
                <NavLink
                  className={({ isActive }) =>
                    ` ${
                      isActive ? "text-black" : "text-black"
                    } relative w-full h-full text-xl rounded-full lg:px-3 lg:py-3  `
                  }
                  to={note.id}
                >
                  {pathname.includes(note.id) ? (
                    <motion.div
                      layoutId="bg-projects-state"
                      className={`absolute inset-0 rounded-full bg-white
                          `}
                      transition={{ type: "spring", duration: 1 }}
                    ></motion.div>
                  ) : null}
                  <span className="relative">{note.title}</span>
                </NavLink>
              </li>
            ))}
          </ol>
        </div>

        <div className="flex-1 ">
          <Outlet />
        </div>
      </main>
    </article>
    // </AnimatePresence>
  );
}
